.contact-preview {
    display: flex;
    flex-direction: column;
    border: 1px solid $clr5;
    width: 250px;
    height: 250px;
    background-color: $clr2;
    padding: rem(10px);
    border-radius: rem(10px);
    justify-content: space-around;
    align-items: center;

    img {
        width: rem(150px);
        height: rem(150px);
        align-self: center;
        object-fit: cover;
    }
    p {
        font-weight: 600;
    }
}
