.loading-transaction {
    width: rem(320px);
}
.transaction-fund {
    background-color: $clr2;
    padding: rem(10px);
    border-radius: rem(10px);
    form {
        display: flex;
        gap: 10px;
        align-items: center;
        label {
            font-weight: 600;
            font-size: rem(20px);
        }
        input {
            width: rem(100px);
        }
        .send-coin-btn {
            background-color: $clr2;
            padding: rem(5px);
            border-radius: rem(10px);
            img {
                width: rem(40px);
            }
        }
    }

    @include for-mobile-layout {
        form {
            .send-coin-btn {
                img {
                    width: rem(25px);
                    // height: rem(40px);
                }
            }
        }
    }
}
