.home-page {
    display: flex;
    flex-direction: column;
    gap: rem(40px);
    // align-items: center;

    border-radius: rem(10px);
    // padding: rem(10px);

    .guest-welcome {
        display: flex;
        flex-direction: column;
        gap: rem(10px);
        align-items: center;
        background-color: $clr2;
        padding: rem(10px);
        border-radius: rem(10px);
        width:fit-content;
        align-self: center;

        svg {
            background-color: rgba(31, 255, 65, 0.7);
            --main-color: $clr5;
            --hover-color: rgba(31, 255, 65, 1);
            // --active-color: yellow;
        }
        p {
            .login-btn {
                color: blue;
            }
            cursor: pointer;
        }
        button {
            border-radius: rem(10px);
            padding: rem(10px);
            background-color: $clr6;
        }
    }

    button {
        width: fit-content;
    }

    @include for-mobile-layout {
        padding: rem(5px);
        gap: rem(20px);
        .guest-welcome {
            h1 {
                font-size: rem(23px);
            }
        }
        .user-balance {
            width: 100%;
        }
    }
}
