.contact-page {
    //   background-color: rgba($color: #fda93f, $alpha: 0.5);
    .contact-header {
        position: sticky;
        background-color: $clr2;
        padding: rem(5px);
        border-radius: rem(10px);
        top: rem(5px);
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        button {
            background-color: $clr2;
            padding: rem(5px);
            border-radius: rem(10px);
        }
        img {
            width: rem(40px);
            height: rem(40px);
        }
    }
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: rem(20px);
    padding: rem(10px);
    button {
        padding: rem(10px);
    }

    @include for-mobile-layout {
        .contact-header {
            form {
                width: rem(200px);
            }
            img {
                width: rem(50px);
                height: rem(50px);
                padding: rem(5px);
            }
        }
    }
}
