.login-loading {
    max-height: 60vh;
    max-width: 60vw;
    position: relative;
    border-radius: rem(10px);
}
.login-form {
    .error-input {
        border: 5px solid red;
    }
    .err-msg {
        color: red;
        font-weight: 600;
    }
}

.error-msg {
    background-color: #cef6ff;
    border: 5px dashed red;
    border-radius: rem(10px);
    color: red;
    display: flex;
    justify-content: center;
}
