.rates-list {
    display: flex;
    text-align: center;
    justify-content: space-evenly;
    // gap: rem(10px);

    .usd-rate,
    .eur-rate,
    .gbp-rate,
    .cny-rate {
        display: grid;
        grid-template-rows: 1fr 1fr 1fr;
        height: rem(150px);
        width: rem(200px);
        padding: rem(10px);
        border-radius: rem(10px);
        border: 1px solid black;
        background-color: #cef6ff;
        // align-items: center;
        // justify-content: center;
        h1 {
            color: #258734;
        }
        img {
            width: auto;
            justify-self: center;
        }
    }

    @include for-mobile-layout {
        // gap: rem(5px);
        h3 {
            font-size: rem(8px);
        }
        h1 {
            font-size: rem(14px);
        }
        p {
            font-size: rem(5px);
        }
        .usd-rate,
        .eur-rate,
        .gbp-rate,
        .cny-rate {
            img {
                width: 25px;
            }
            padding: rem(5px);
            height: rem(60px);
            width: rem(60px);
            h1 {
                font-size: rem(12px);
            }
        }
    }
}
