.contact-details-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: rem(20px);
    padding: rem(10px);
    

    .contact-details {
        width: rem(500px);
        height: rem(500px);
        position: relative;
        display: flex;
        flex-direction: column;
        padding: rem(10px);
        border-radius: rem(10px);
        background-color: $clr2;
        gap: rem(10px);

        .contact-img-details {
            flex: 1;
            display: flex;
            img {
                max-height: 350px;
                object-fit: cover;
            }
        }

        .edit-logo {
            background-color: $clr2;
            position: absolute;
            right: 0;
            bottom: 0;
            display: flex;
            justify-content: space-between;
            border-radius: rem(10px);
            padding: rem(5px);
            img {
                width: rem(40px);
            }
        }
    }

    // .MuiPaper-root {
    //     text-align: center;
    //     width: max-content;
    // }

    @include for-mobile-layout {
        align-items: unset;

        .contact-details {
            width: auto;
            height: 350px;
            gap: rem(5px);
            .contact-img-details {
                align-self: center;
                .contact-img {
                    max-height: 250px;
                }
            }
        }
        // .MuiPaper-root {
        //     width: 100%;
        // }
        .edit-logo {
            img {
                width: rem(25px);
            }
        }
    }
}
