.app-header {
    padding: 0 rem(10px);
    
    margin-inline: rem(70px);
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $clr2;
    border-radius: rem(10px);
    position: sticky;
    top: 0;
    z-index: 1;

    .app-logo {
        cursor: pointer;
        border: 1px solid black;
        border-radius: 50%;
        img {
            width: rem(50px);
        }
    }

    .MuiTypography-root {
        font-size: rem(15px);
    }
    .MuiBox-root {
        display: flex;
        flex-direction: row;
        gap: 0;
    }

    .header-fullname {
        font-weight: 600;
    }
    .MuiButtonBase-root {
        padding: 0;
    }

    @include for-mobile-layout {
        margin-inline-start: 0;
        margin-inline-end: 0;
        justify-content: flex-start;

        .app-logo {
            img {
                width: rem(50px);
            }
        }
        .MuiButtonBase-root {
            margin-left: rem(8px);
        }
        div:last-child {
            margin-inline-start: auto;
            // margin-inline-end: rem(5px);
        }

        div:first-child {
            margin-inline-start: rem(5px);
        }

        .MuiAvatar-root {
            width: rem(50px);
            height: rem(50px);
        }
    }
}
