.user-details-page {
    display: flex;
    flex-direction: column;
    justify-self: center;
    background-color: $clr2;
    padding: rem(10px);
    border-radius: rem(10px);
    margin-block-start: rem(10px);
    img {
        width: rem(500px);
    }

    @include for-mobile-layout {
        img {
            width: rem(250px);
        }
    }
}
