.transactions-table {
    .MuiPaper-root {
        background-color: $clr2;
        border-radius: rem(10px);
        border: 1px solid black;
    }
    .MuiTypography-root {
        text-align: center;
    }

    .amount-plus {
        color: #2bd540;
        font-weight: 600;
    }
    .amount-minus {
        color: red;
    }

    @include for-mobile-layout {
        width: 100%;
        .MuiTableCell-root {
            min-width: 0px;
        }
        .MuiTypography-root {
            font-size: rem(22px);
        }

        .MuiTable-root {
            min-width: auto;
        }
        margin-inline-start: 0;
        margin-inline-end: 0;
        .MuiTableCell-root {
            padding: rem(3px);
            font-size: rem(7px);
        }
    }
}
